import React from "react";
import "./soft_roof.css";

const SoftRoof = ({ onOrderClick }) => {
  return (
    <div className="Soft_roof mrg-top">
      <div className="Soft-roof_special-container roof_special-container">
        <h1 className="Soft_roof-title roof-title">М’яка покрівля</h1>
        <p className="Soft-roof_special-text roof_special-text">
          М'яка покрівля – це універсальне рішення для надійного захисту вашої
          будівлі. Ми професійно працюємо з руберойдом та бітумною черепицею,
          виконуючи укладання, заміну та ремонт цих матеріалів. Наша команда
          проводить повну підготовку поверхні, усуває дефекти основи та акуратно
          монтує покриття. Це гарантує довговічність та бездоганний зовнішній
          вигляд вашого даху, а також захист від протікання.
        </p>
        <div className="Soft_roof_bot-div roof_bot-div">
          <h3> Переваги:</h3>
          <ul className="special-text_list list">
            <li className="special-text_item">
              Доступна вартість матеріалів та робіт.
            </li>
            <li className="special-text_item">
              Швидке встановлення навіть на складних ділянках.
            </li>
            <li className="special-text_item">
              Чудова шумоізоляція під час дощу чи граду.
            </li>
          </ul>
        </div>
        <div
          className="Soft-roof_btn-container btn-special_container"
          onClick={onOrderClick}
        >
          <div className="feedback-container">
            <div className="feedback-border">
              <div className="feedback">
                <div className="feedback-text-container">
                  <p className="feedback-text">Замовити дзвінок</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <img
        className="Soft-roof_special-img roof_special-img"
        src="./img/soft-roof.jpg"
        alt=""
      ></img>
    </div>
  );
};

export default SoftRoof;
